@import url('https://fonts.googleapis.com/css2?family=Sarabun:wght@300&display=swap');
.card-title{
    text-align:center;
    padding-top: 6%;
    font-family: 'Sarabun', sans-serif;
}
.card-body{
    font-size: 30pt;
    font-family: 'Sarabun', sans-serif;
    text-align: center;
}
.confirm_selection {
    animation: glow 3s infinite alternate;
}

@keyframes glow {
    to {
        text-shadow: 0 0 10px #0B132B;
    }
}

.abticons{
    color: #434261;
    align-content: center;
}
.me{
    width: 60%;
    height: 60%;
}

.confirm_selection {
    font-family: sans-serif;
    font-size: 100pt;
    font-weight: bold;
}
.abtCard{
    width: 98%;
    height:700px;
    margin: 0 auto;
    border-radius: 20%;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
